.map {
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
    height: 100%;
}

.info-flag img {
    width: 100px;
    border-radius: 5px;
}

.info-flag {
    height: 99px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.info-container {
    width: 150px;
}

.info-confirmed,
.info-deaths,
.info-recovered {
  font-size: 12px;
  margin-top: 5px;
}

.info-name {
  font-size: 16px;
  font-weight: bold;
  color: #555;
}